@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.desc {
	 padding-top: 170px;
	 display: flex;
	 flex-direction: row;
	 flex-wrap: wrap;
	 justify-content: center;
}
 .desc article {
	 margin: 0 50px 0 50px;
	 overflow: hidden;
}
 .textImage {
	 position: absolute;
	 width: 380px;
	 height: 280px;
	 color: #db0000;
	 background-color: rgba(219, 0, 0, 0.8);
	 z-index: 2;
	 overflow: hidden;
	 -webkit-clip-path: polygon(10% 0, 100% 0%, 100% 100%, 81% 100%);
	         clip-path: polygon(10% 0, 100% 0%, 100% 100%, 81% 100%);
}

.desc article img:hover {
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
}

.textImage h3 {
	 position: relative;
	 font-size: 26px;
	 top: 40px;
	 left: 160px;
	 height: 100px;
	 color: white;
	 font-family: 'Roboto', sans-serif;
}
 .desc article .textImage P {
	 cursor: pointer;
	 position: absolute;
	 font-size: 15px;
	 left: 250px;
	 color: white;
	 font-family: 'Roboto', sans-serif;
}
 .desc article img {
	 background-color: #e4e7ed;
	 position: static;
	 position: initial;
	 z-index: 1;
	 width: 380px;
	 height: 280px;
	 transition: all 0.5s;
}
 

@media(max-width: 472px){
	.desc article {
		overflow: visible;
		margin: 0 0 0 0;
   }
   article img:hover {
	-webkit-transform: scale(100%) !important;
	        transform: scale(100%) !important;
}
}

@media(max-width: 450px){
	.desc  {
		width: 100%;
		margin-left: 0px;
   }
}
@media(max-width: 400px){
	.desc article{
		width: 80px;
	}
	.textImage,.desc article img{
		width: 250px;
	}
	
	.desc  {
		width: 100px;
		align-self: center;
		margin-left: 20px;
   }

   .textImage h3{
	   position: relative;
	   top: 80px;
	   left: 100px;
	   font-size: 14px;
   }
}
